@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Open+Sans|Oswald:500");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 18px;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.primary {
  color: #003359;
}

.dark {
  color: #406683;
}

.bold {
  color: #a6b8c5;
}

.warning {
  color: #f12c3e;
}

div.primary {
  background: #f9f5f2;
}

div.dark {
  background: #003359;
}

div.bold {
  background: #406683;
}

div.warning {
  background: #f9acb3;
}

strong {
  color: #a6b8c5;
}

.bold strong {
  color: #003359;
}

.warning strong {
  color: #f9f5f2;
}

h1, h2, h3, blockquote {
  font-family: "oswald", sans-serif;
  font-weight: 500;
  line-height: 1.2;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.7;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
}

blockquote {
  margin-top: 1rem;
  font-size: 1.7rem;
  line-height: 1.3;
}

blockquote:before {
  color: #ccc;
  content: '“';
  font-size: 4.5rem;
  line-height: 0.1rem;
  margin-left: -2.6rem;
  margin-right: 0.75rem;
  vertical-align: -2rem;
}

@media (min-width: 968px) {
  .collapse-tablet-down {
    flex-basis: 100%;
  }
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .row {
  display: -webkit-flex;
  display: flex;
  box-sizing: border-box;
  flex-direction: row;
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .row {
    flex-direction: column;
    flex-basis: 100%;
  }
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
  margin: 0;
  padding: 0;
}

